@import url(https://fonts.googleapis.com/css2?family=Play&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Play:wght@700&display=swap);
body {
  margin: 0;
  font-family: 'Play', sans-serif, 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#cryptape {
  position: absolute;
  top: 0;
  right: -2px;
}

h1 {
  font-family: 'Play', sans-serif;
  font-size: 60px;
  color: #B8B8B8;
  margin: 0;
  text-align: right;
  line-height: 0.75em;
}

#search-box {
  position: absolute;
  top: 0;
  left: 0;
}

#legend {
  position: absolute;
  right: 0;
  bottom: 170px;
}

#decoration-top {
  position: absolute;
  right: 0;
  top: 55px;
}

#decoration-bottom {
  position: absolute;
  right: 0;
  bottom: 0;
}

#bottom-line {
  position: absolute;
  right: 0;
  bottom: 0;
}

#tx-viz {
  position: absolute;
  right: 0;
  bottom: 2.5px;
}

@media screen and (max-width: 1024px) {
  h1 {
    font-size: 40px;
  }

  #decoration-top {
    top: 35px;
  }
}

@media screen and (max-width: 769px) {
  #legend {
    bottom: 100px;
  }
  #decoration-top,
  #decoration-bottom,
  #bottom-line,
  #tx-viz,
  #search-box {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  h1 {
    font-size: 30px;
  }

  #cryptape {
    position: absolute;
    top: -30px;
    right: 0;
  }

  #legend,
  #decoration-top,
  #decoration-bottom,
  #bottom-line,
  #search-box {
    display: none;
  }

}

@media screen and (max-height: 400px) {
  #cryptape,
  #legend,
  #decoration-top,
  #decoration-bottom,
  #bottom-line,
  #search-box {
    display: none;
  }
}

#tx-list {
  width: 24vw;
  position: absolute;
  bottom: 0;
  left: 0;
}

li {
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
  color: #b8b8b8;
  font-size: 11px;
  list-style-type: none;
  margin-top: 9px;
}

li:nth-child(1) {
  opacity: 1;
}

li:nth-child(2) {
  opacity: 1;
}

li:nth-child(3) {
  opacity: 1;
}

li:nth-child(4) {
  opacity: 0.9;
}

li:nth-child(5) {
  opacity: 0.8;
}

li:nth-child(6) {
  opacity: 0.6;
}

li:nth-child(7) {
  opacity: 0.6;
}

ul {
  padding: 0;
  margin: 0;
  padding-bottom: 2.5px;
}

@media screen and (min-width: 769px) {
  ul,
  ul li {
    -webkit-transform: scaleY(-1);
    transform: scaleY(-1);
  }

  li {
    cursor: pointer;
  }
}

@media screen and (max-width: 1024px) {
  li {
    font-size: 7px;
    margin-top: 4px;
  }
}

@media screen and (max-width: 769px) {
  #tx-list {
    width: 100%;
    top: 250px;
  }
}

@media screen and (max-width: 480px) {
  li {
    font-size: 5px;
  }

  #tx-list {
    width: 100%;
    position: absolute;
    top: 150px;
    left: 0;
  }
}

.timeline-slider{-webkit-appearance:none;width:500px;height:2.5px;background:#e6e6e6;outline:none;transition:opacity 0.2s}.timeline-slider::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;width:5px;height:15px;background:#18efb1;cursor:pointer}.timeline-slider::-moz-range-thumb{width:5px;height:15px;background:#18efb1;cursor:pointer}#timeline>*{vertical-align:middle}#play-button{margin-left:10px;background:transparent;width:10px;border:none;cursor:pointer}#bg{-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px)}#tx-nodes{position:absolute;top:0}.timeline-time{display:inline-block;color:#18efb1;font-size:11px;margin-right:10px}#timeline{position:absolute;bottom:15px;margin-left:22px}#cancel-button{background-color:transparent;width:12px;cursor:pointer;position:absolute;top:22px;right:22px}#tx-hash{color:#e6e6e6;position:absolute;top:22px;left:22px;font-size:11px;width:150px;word-break:break-all;margin:0}#corner0{-webkit-transform:rotate(0deg);transform:rotate(0deg);position:absolute;width:12px;top:10px;left:10px}#corner1{-webkit-transform:rotate(90deg);transform:rotate(90deg);position:absolute;width:12px;top:10px;right:10px}#corner2{-webkit-transform:rotate(180deg);transform:rotate(180deg);position:absolute;width:12px;bottom:10px;right:10px}#corner3{-webkit-transform:rotate(-90deg);transform:rotate(-90deg);position:absolute;width:12px;bottom:10px;left:10px}

*{box-sizing:border-box}.search{width:30px;height:30px;margin-top:2.5px;background-color:rgba(31,31,31,0.8);position:relative;overflow:hidden;transition:all 0.5s ease;-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px)}.search:before{content:'';display:block;width:2px;height:100%;position:relative;background-color:#18EFB1;transition:all 0.5s ease}.search.open{width:24vw}.search.open:before{height:18px;margin:6px 0 6px 9px;position:absolute}.search-input{width:100%;height:100%;box-shadow:none;border:none;background:transparent;color:#E6E6E6;padding:6px 30px 6px 13.5px;font-size:10px;font-family:'Play', sans-serif}.search-input:focus{outline:none}.search-button{width:30px;height:30px;display:block;position:absolute;right:0;top:0;padding:6px;cursor:pointer}.search-icon{width:12px;height:12px;border-radius:12px;border:2px solid #18EFB1;display:block;position:relative;margin-left:1.5px;transition:all 0.5s ease}.search-icon:before{content:'';width:1.5px;height:4.5px;position:absolute;right:-0.6px;top:9px;display:block;background-color:#18EFB1;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);transition:all 0.5s ease}.search-icon:after{content:'';width:1.5px;height:4.5px;position:absolute;right:-3.6px;top:12px;display:block;background-color:#18EFB1;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);transition:all 0.5s ease}.open .search-icon{margin:0;width:18px;height:18px;border-radius:18px}.open .search-icon:before{-webkit-transform:rotate(52deg);transform:rotate(52deg);right:5.8px;top:6.5px;height:5.4px}.open .search-icon:after{-webkit-transform:rotate(-230deg);transform:rotate(-230deg);right:5.8px;top:3.5px;height:5.4px}input[type="search"]::-webkit-search-cancel-button{-webkit-appearance:none;width:0;pointer-events:none}

